var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美膚光療")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("電波拉提")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"電波拉提","titleEn":"THERMAGE"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/thermage/banner.jpg")))+" alt=\"電波拉提 Banner\"> <p>過了25歲，除了基礎代謝率明顯下滑外，膠原蛋白增生的速度也開始來不及補充流失的部分，肌膚開始出現鬆弛無彈性、毛孔粗大以及暗沉的現象。隨著科技的日新月異，現在只要短短的二小時，就能立即感受到拉提的效果，伴隨著傷口小疼痛指數低，無恢復期的優勢，術後更可以照常上班、上妝。輕輕鬆鬆守住青春防線!</p></div> <div class=\"article-container\"><h2>透過結合光能及電磁波的優點</h2> <p>電波拉皮是運用600萬赫茲無線電波穿透皮膚層的原理，在真皮下層及皮下組織中產生熱量，促使膠原纖維立即收縮重組，並製造新的膠原蛋白，並運用增加cooling treat冷媒脈衝，干擾大腦神經傳導疼痛反應，以提高治療過程中的舒適感並達到緊實肌膚、減少臉部紋路及臉型雕塑的效果。</p> <iframe src=\"https://www.youtube.com/embed/DE79hMOPy4Y\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"allowfullscreen\"></iframe></div> <div class=\"article-container\"><h2>電波緊顏緊緻肌膚二重奏</h2> <h3>第一重立即作用：膠原蛋白的收縮</h3> <p>真皮層因收縮而產生緊緻的效果皮下組織層的纖維中隔(Fibrous Septae)因收縮而產生塑形效果、可以改善鬆弛。</p> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/thermage/1.png")))+" alt=\"膠原蛋白的收縮案例\"> <p>受治療的左半邊臉，治療完立即就可感到緊緻喔！</p> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h3>第二重長期作用：膠原蛋白的新生與重組</h3> <p>膠原蛋白再生、重組隨著時間不斷的產生，持續緊緻皮膚 、效果因而持久。</p> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/thermage/案例1.png")))+" alt=\"膠原蛋白的新生與重組案例\"> <p>多回治療，經過兩個月後，臉部細紋減少，緊緻拉提。</p> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h2>療程特色</h2> <ul><li>+ 美國研發、US FDA核准</li> <li>+ 經台灣衛生福利部核准</li> <li>+ 適用所有膚質，一次療程即可見效</li> <li>+ 無創、臨床證明安全性極佳</li> <li>+ 幾乎不需恢復期、日常生活不受影響</li> <li>+ 針對全臉、眼皮、身體各有專屬療程</li></ul></div> <div class=\"article-container\"><h2>療程治療區域</h2> <h3>⦿ 臉部</h3> <p>眼周、全臉、下顎線、下巴、脖子、唇部</p> <h3>⦿ 身體部位</h3> <p>手臂(蝴蝶袖)、手背、腹部、腰部、臀部、大腿</p> <h3>⦿ 眼睛</h3> <p>鬆弛的上 / 下眼瞼、魚尾紋、眼周細紋</p> <h3>⦿ 頑固性青春痘</h3> <h3>⦿ 青春痘形成之凹洞(圓盤狀)</h3></div> <div class=\"article-container\"><h2>美麗案例</h2> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/thermage/案例3.png")))+" alt=\"案例3\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/thermage/案例4.png")))+" alt=\"案例4\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/thermage/案例5.png")))+" alt=\"案例5\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/thermage/案例6.png")))+" alt=\"案例6\"> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h2>術前小提醒</h2> <ul><li>- 曾經做過多次煥膚者(視情況)、嚴重異位性皮膚炎、免疫系統異常不建議進行治療。</li> <li>- 嚴重化膿的皮膚或嚴重青春痘的皮膚、特殊皮膚情況者…等等不建議進行治療。</li> <li>- 曾經使用其他雷射後嚴重反黑者不建議進行治療。</li> <li>- 有心臟性疾病、裝置有心律調整器及孕婦不建議進行治療。</li> <li>- 一個月內有做微整形或使用換膚產品者，請事先告知醫師。</li> <li>- 治療範圍曾注射人工填充物，如：矽膠，不建議進行治療。</li> <li>- 治療時身上嚴禁佩戴任何金屬物品。</li> <li>- 請務必告知醫護人員、治療範圍是否曾經動過手術。</li> <li>- 裝置子宮避孕器、施打過肉毒桿菌並不會影響電波拉皮的治療效果。</li></ul></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>- 術後一周內禁止使用磨砂膏、A酸、果酸、美白產品或高單位濃度的藥品及保養品，若有特殊使用產品需先告知醫師或醫護人員。</li> <li>- 治療後一周內請勿泡溫泉、游泳、洗三溫暖、蒸氣或烤箱(避免身體水份過度流失及皮膚過度乾燥)。</li> <li>- 治療後不會造成傷口，若有溫熱感會於數小時內消失，少數人治療後有輕微腫脹情形，3-5天即可消退。</li> <li>- 治療後即可使用保養品，並加強保濕及防曬，彩妝品視皮膚情況使用。</li> <li>- 治療後一至二周為修護的黃金週期，故術後當天請開始加強保濕及防曬，療效會有加倍功效。</li> <li>- 治療部位大約1-2周感覺到有的緊緻感，約3個月後緊緻拉提的效果慢慢出現，且越來越明顯。</li> <li>- 「電波拉皮」的效果依施打的部位及個人的的保養方式不同，大約可以維持一至二年，經過多次的治療，可以達到更加的效果，建議半年~一年後可以進行治療。</li> <li><h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></li></ul></div> <div class=\"article-container\"><h2>常見問題</h2> <h3>Q. 電波拉皮療效多久可以見效</h3> <p>在接受Thermage電波拉皮一週內就有顧客開始感受到逐漸的改善，多數人在接受臉部療程2個月後會開始感受到改善，3-6個月後改善最為明顯</p> <h3>Q. 過程中會痛嗎？是否需要麻醉？</h3> <p>過去電波拉皮確實給人疼痛度高的印象，但是第三代CPT電波已經大幅改善疼痛問題，過程中會產生局部冰涼但偶有微燙的觸感。電波拉皮最初的研發理念即為「不需要麻醉的醫美療程」醫師會透過與您的互動，調整最適合的電波能量等級。</p> <h3>Q. 整個療程會花多少時間</h3> <p>將視施作面積有所不同，平均為45-90分鐘。</p> <h3>Q. 療程後需要休息多久</h3> <p>在療程結束當下皮膚可能會出現些許紅腫，大部分的人無須休息即可進行日常行程，因此表皮完全無傷口。亦可正常上妝。</p> <h3>Q. 療程結束後，身邊的人會發現我的皮膚有異狀嗎</h3> <p>由於幾乎不需要恢復期、不影響工作及社交、外觀的改善來自人體組織與生俱來的修護機制，效果自然不易讓旁人發現明顯異狀，但是隨時時間將察覺到您的氣色、質膚、輪廓線條的改善，旁人會感受到你的容光煥發、魅力丰采升級。</p> <h3>Q. 電波拉皮和其他緊膚拉提的療效有什麼不同</h3> <p>電波拉皮是目前醫界擁有超過14年臨床經驗和實驗的醫學美容療程，其安全性也經過廣大醫師和病人的考驗，其獨家的熱能傳導科技在臨床上呈現的效果風靡全球醫美界，有目共睹。</p></div>")],2)}
